<template>
  <div>
    <!-- 顶部登录 -->
    <div class="header">
      <van-nav-bar
        :border="navBorder"
        title="个人中心"
        left-arrow
        :right-text="text"
        @click-left="$router.push('/home')"
        @click-right="del"
      ></van-nav-bar>
      <div class="user" v-if="!$store.state.loginBool">
        <router-link class="register" to="/register">注册</router-link>
        <i class="line"></i>
        <router-link class="login" to="/login">登录</router-link>
      </div>

      <div class="my" v-if="$store.state.loginBool">
        <label class="box-img">
          <input type="file" style="display:none" @change="changeImg" />
          <img
            :src="imgUrl"
            alt
          />
        </label>
        <div class="my-user">
          <div class="username">{{$store.state.username}}</div>
          <div class="my-account">我的账户</div>
        </div>
      </div>

      <van-cell-group>
        <van-cell title="我的订单" value="全部订单" @click="allOrders" />
      </van-cell-group>

      <van-grid>
        <van-grid-item icon="cash-on-deliver" text="待付款" @click="obligation" />
        <van-grid-item icon="user-circle-o" text="待发货" @click="beShipped" />
        <van-grid-item icon="logistics" text="待收货" @click="beReceived" />
        <van-grid-item icon="after-sale" text="退款/售后" />
      </van-grid>

      <van-cell-group>
        <van-cell title="我的收货地址" @click="receivingAddress" />
        <van-cell title="我的收藏" @click="collection" />
        <van-cell title="我的优惠券" @click="discount" />
        <van-cell title="我的免单卷" value="0张" @click="$router.push('/home')" />
        <van-cell title="余额" :value="$store.state.balance.toFixed(2)" @click="surplus" />
        <van-cell title="关于卷皮" @click="$router.push('/juanpi')" />
      </van-cell-group>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      navBorder: false,
      text: "",
      imgUrl:'https://face1.juancdn.com/face/150918/1/0/055fb732fce80d_640x640.jpg?iopcmd=thumbnail&type=11&height=80&width=80%7Ciopcmd=convert&Q=88&dst=jpg'
    };
  },
  methods: {
    // 收货地址
    receivingAddress() {
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$router.push("/receivingAddress");
    },
    // 优惠卷
    discount() {
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$router.push("/discount");
    },
    // 待付款
    obligation() {
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$router.push({ path: "/allOrders", query: { num: 1 } });
    },
    // 代发货
    beShipped() {
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$router.push({ path: "/allOrders", query: { num: 2 } });
    },
    // 待收货
    beReceived() {
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$router.push({ path: "/allOrders", query: { num: 3 } });
    },
    // 全部订单
    allOrders() {
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$router.push("/allOrders");
    },
    // 余额
    surplus() {
      if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$router.push("/surplus");
    },
    // 退出
    del() {
      this.text = "";
      this.$store.state.loginBool = false;
      this.$store.state.username = null;
      this.$store.state.balance = 0;
    },
    // 头像
    changeImg(e) {
      var _this = this
      // console.log(e.target.files);
      for (var i = 0; i < e.target.files.length; i++) {
        var file = e.target.files.item(i);
        var freader = new FileReader();
        freader.readAsDataURL(file);
        freader.onload = function(e) {
          var src = e.target.result;
          _this.imgUrl = src
          // console.log(this.imgUrl);
        };
      }
   
    },
    // 我的收藏
    collection(){
       if (this.$store.state.loginBool == false) {
        this.$router.push("/login");
        return;
      }
      this.$router.push('/collection')
    }
  },
  created() {
    this.$store.state.bool = true;
    if (this.$store.state.username) {
      this.text = "退出";
    } else {
      this.text = "";
    }
  }
};
</script>

<style lang="scss" scoped>
.header {
  background: #ff464e;
  background-image: url("https://jp.juancdn.com/jpwebapp_v1/images_v1/user/user_center_bg.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  border-bottom: none;
  .van-nav-bar {
    background: transparent;
    color: #fff;
    .van-icon {
      color: #fff;
      font-size: 18px;
    }
    .van-nav-bar__text {
      color: #fff;
      background: transparent;
    }
  }
  .van-nav-bar__title {
    color: #fff;
    font-size: 18px;
  }
  .user {
    line-height: 100px;
    text-align: center;
    .line {
      display: inline-block;
      height: 20px;
      width: 2px;
      background-color: #fff;
    }
    a {
      display: inline-block;
      height: 100px;
      padding: 0 48px;
      color: #fff;
      font-size: 20px;
    }
  }
  .van-cell {
    font-size: 16px;
  }
}
.van-cell__value {
  span {
    font-size: 14px;
  }
}
.my {
  height: 100px;
  overflow: hidden;
  &::after {
    display: block;
    clear: both;
    content: "";
  }
  .box-img {
    margin-left: 20px;
    width: 60px;
    border-radius: 50%;
    overflow: hidden;
    margin-top: 20px;
    float: left;
    img {
      width: 100%;
      height: 60px;
    }
  }
  .my-user {
    height: 60px;
    margin-top: 25px;
    float: left;
    margin-left: 20px;
    color: #fff;
    font-size: 18px;
  }
  .my-account {
    display: inline;
    font-size: 14px;
    border: 1px solid #fff;
    border-radius: 45px;
  }
}
</style>